@import './assets/css/fonts';
@import './assets/css/variables';
*{
  @include margin(0);
  @include padding(0);
}
body{
  @include padding(0);
  @include margin(0);
  box-sizing: border-box;
  @include font__family(14px,'',normal,30px,'Montserrat-Regular',0,normal,normal);
}
p{
  @include font__family(14px,'',normal,30px,'Montserrat-Regular',0,normal,normal);
  @include padding(0);
  @include margin(0);
}
.profile__header{
  padding: 30px 0;
  background: #2f3a40;
}
.profile_image{
  width: 100%;
}
.profile__contact{
  @include padding(30px 0);
}
.profile__contact__row__1{
  background: #fc5857;
}
.profile__contact__row__2{
  background: #b6c433;
}
.profile__contact__row__3{
  background: #589ab9;
}
.profile__contact__row__4{
  background: #fd8d36;
}
.profile__contact__h__listing{
  color: #fff;
  font-size: 1rem;
  .profile__contact__s__p{
    position: relative;
    top: -6px;
  }
}
.profile__contact__h__ul{
  li{
    span{
      display: inline-block;
      width: calc(100% / 4);
    }
  }
}
.profile__contact__heading{
  @include font__family(26px,#fff,normal,1.5,'Montserrat-Medium',1,normal,normal);
  @include text__transform(uppercase);
}
.no-padding{
  padding-left: 0;
  padding-right: 0;
}
.pull__left__2{
  left: 10%;
}
.loader{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform: -webkit-translate(-50%, -50%);
  transform: -moz-translate(-50%, -50%);
  transform: -ms-translate(-50%, -50%);
}